@import url('https://fonts.googleapis.com/css2?family=Agdasima:wght@400;700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

:root{
    --main-bg-color: rgb(198, 218, 108) ;
 }
nav{
    width: 30%;
    height: 750px;
    background-color: rgb(198, 218, 108) ;
    display: flex;
    flex-direction: column;
    border-bottom-left-radius: 350px;
    align-items: center;
    border-top-left-radius: 30px;
    background-image: url('../images/background-nav.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top left;
    overflow: hidden;
    z-index: 1;
    position: relative;
}
.navbar__menus{
    display: flex;
    color: white;
    justify-content: space-evenly;
    width: 80%;
    position: relative;
    font-family: 'Poppins', sans-serif;
    z-index: 1;
    height: 50px;
    background-color: white;
    border-radius: 25px;
    margin: 10px;
    
}
.navbar__menus h3:nth-child(2):after{
    content: " ";
    background-color: black;
    position: absolute;
    height: 0px;
    width:20%;
    top: 45px;
   
    transition: height 0.5s ease; 
    
   
}
.navbar__menus h3:hover::after {
    height: 150px; /* Adjust the height value as per your requirements */
  }

    

.navbar__menus h3{
    cursor: pointer;
    font-weight: 500;
    font-size: 14px;
    height: 100%;
    width: 20%;
    color: black;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
   
    
    
  
}

.navbar__menus h3:hover{
        background-color: black;
        color: white;
        height: 100%;
        width: 20%;
        
}

@keyframes sub-menu{
    0%{
        height: 140px;
    }
    100%{
        height: 0px;
    }
} 

.navbar__image{
    background-image: url('../images/old_emo-removebg-preview.png');
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: 10px 0 ;
    background-size: cover;
    z-index: 1;
}
@media only screen and (max-width: 844px) {
   
    nav{
      width: 100%;
    }
   
  }
