
@import url('https://fonts.googleapis.com/css2?family=Agdasima:wght@400;700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&family=Roboto:wght@300;400;500;700;900&display=swap');


@import url(./styles/navbar.css);

html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  
 
}
  #root {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    overflow:visible;
    height: 100%;
    
    
  }
  
  .App {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    position: relative;
    width: 100%;
    flex-wrap: wrap ;
    height: 100%;
    max-width: 1500px;
   
    /* background-image: url('./images/wave-haikeiBackground.png'); */
    
  }
  @media only screen and (max-width: 654px) {
    html, body{
      height: 100vh;
      width: 100%;
      margin: 0;
      padding: 0;
      display: flex;
      justify-content: center;
      
    
    }
    body{
      
      background-color: var(--main-bg-color);
      
     
    }
    #root{
      display: flex;
      justify-content: center;
      align-items: center;
        
      margin: 0;
      padding: 0;
      width: 100%;
      height: 100%;
      
    }
    .App{
      height: 100vh;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
     
      
      gap: 0;

      margin:0;
      
      padding: 0;
      
        }

    nav{
      order: 1;
      background-image: none;
      background-color:unset;
      height: 80px;
     margin-bottom: 0;
      position: relative;
      width: 100%;
     
      border-radius: 0;
      

    }
    .navbar__image{
      display: none;
    }

    .info{
      order: 2;
      color: white;
      height: 950px;
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      margin: 0;
      padding-top: 0;
      overflow: visible;
      padding: 0;
      justify-content: flex-start;
      margin-bottom: 20px;
      padding-bottom: 0px;
      background-position: top right;
      width: 100%;
      
      
      
    }
    .info__services{
      flex-direction: row;
      flex-wrap: wrap;
      width: 100%;
    }
    .info__services-item{
      width: 70%;
      margin-bottom: 25px;
      background-color: rgb(255, 255, 255, 0.2);
      border: 0.5px white solid;
      color: black;
      height: 220px;
      
    }
    .info__services-item h2{
      margin-bottom: 0;
      padding-bottom: 0;
      margin-top: 0;
      padding-top: 0;

    }
    .info__services-item p{
      margin-bottom: 10px;
      padding-bottom: 0;
      margin-top: 10px;
      padding-top: 0;
      

    }
    .info__services-item i{
      font-size: 20px;
      margin-bottom: 0;
      padding-bottom: 0;
      margin-top: 0px;
      padding-top: 0;
      
      

    }
    .info h1{
      color: white;
      font-size: 25px;
    
    }
    .aktuelles{
    
      display: flex;
      flex-direction: column;
      overflow: hidden;
      justify-content: flex-start;
      flex: 1;
      height: 220px;
     width: 100%;
     height: 450px;
      order: 3;
      margin-top: 0px;
      border-top-right-radius: 25px ;
      background-image: url('./images/siteImages/My_project.png');
      background-repeat: no-repeat;
      background-size: cover;
      background-position: -150px 0;

      
      padding-top: 20px;
     
  
    }
    .aktuelles__title{
      width: 70%;
      margin-left: auto;
      
    }
    .aktuelles__title h1{
      font-size: 25px;
      line-height: 35px;
      height: 40px;
      width: 100%;
      text-align: center;
      padding-right: 10px;
      color: white;
   
      


    }
    .aktuelles__title p{
      font-size: 12px;
      height: 120px;
      padding: 30px 15px 0px 40px;
      text-align: right;
      
    }
    .aktuelles__cards{
      visibility: hidden;

    }
    .mission{
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      margin: 0;
      justify-content: flex-start;
      padding: 0;
    border-radius: 40%;
    overflow: hidden;
      order: 4;
     width: 100%;
     height: 400px;
    }
    .mission__images{
      width: 100%;
      height: 250px;
      order: 2;
      
    }
    .mission__title{
      width: 100%;
      height: 50%;
      
    }
    .mission__title h1{
      font-size: 25px;
      margin-top: 30px;
      padding-top: 0;
      color: white;
    }
    .mission__title p{
      font-size: 12px;
      margin-top: 0px;
      
      width: 80%;
    }
    .mission button{
      width: 90%;
      height: 35px;
     
    }
    footer{
      display: flex;
      width: 100%;
      order: 5;
      background-image: url('./images/blob-haikei.png');
      background-size: cover;
      background-position: bottom center;
      height: 500px;
      padding-bottom: 20px;
      margin-bottom: 50px;
      margin-top: 50px;
    }
    .detail-holder{
      height: 100px;
      padding-bottom: 50px;
        
    }
    .detail-holder p{
      margin-bottom: 50px;
    }
    h5{
      text-align: center;
      margin-top: 10px;
    }
  }

  @media only screen and (min-width: 655px) and (max-width: 786px) {
    .App{
      flex-direction: column;
      justify-content: flex-start;
    }
    nav{
      order: 1;
    }
    .navbar__image{
      background-position: 100px  bottom;
    }
    .info{
      order: 2;
      
    }
    .info__services{
      margin-top: 10px;
      height: 300px;
    }
    .info__services-item{
      justify-content: flex-start;
      
    }
    .info__services-item h2{
      font-size: 12px;
      font-weight: 600;
      text-align: center;
      margin-bottom: 0px;
    }
    .info__services-item p{
      text-align: center;
    }
  
    .aktuelles{
      order: 4;
      height: 300px;
      width: 100%;
      overflow: hidden;
      border-top-right-radius: 30% ;
      margin:0;
    }
    .aktuelles__title{
      width: 100%;
      height: 120px;
      flex-direction: row;
      justify-content: start;
      align-items: left;
      margin: 0;
      padding: 0;
      flex-wrap: wrap;
    }
    .aktuelles__title h1{
      flex-basis: 30%;
      margin: 0;
      padding: 0;
      text-align: left;
      margin-left: 50px;
      font-size: 25px;
      line-height: 35px;
    }
    .aktuelles__title p{
      font-size: 12px;
      width: 60%;
      height: 100%;
      flex-basis: 40%;
    }
    .aktuelles__cards h2{
      font-size: 12px;
    }
    .aktuelles__cards{
      width: 100%;
      flex-direction: row;
      gap: 5px;
      
      justify-content: center;
      margin-top: 0;
      
     
    }
    .aktuelles__cards-card{
      width: 100px;
      height: 160px;
      overflow: hidden;
      
      gap: 0;
      margin: 5px;
    }

    .mission{
      position: relative;
      order: 3;
      height: 400px;
      justify-content: center;
      align-items: center;
    }
    .mission__title{
      position: absolute;
      width: 70%;
      height: 70%;
      background-color: rgb(255,255,255, 0.7);
      border-radius: 50px;
      padding: 35px;
      
    }
    .mission__images{
      width: 100%;
    }
    footer{
      order: 5;
      width: 100%;
      flex-direction: row;
      height: 300px;
      background-position: bottom;
      flex-wrap: wrap;
    }
    .detail-holder{
      flex-basis: 40%;
      height: 100px;
    }
    .info__services-item{
      width: 50%;
      margin-bottom: 25px;
      background-color: rgb(255, 255, 255, 0.2);
      border: 0.5px white solid;
      color: black;
      height: 220px;
      
    }

  }
  @media only screen and (min-width: 787px) and (max-width: 1200px) {
    .App{
      flex-direction: row;
      flex-wrap: wrap;
    }
    nav{
      order: 2;
      width: 50%;
      border-bottom-left-radius: 200px;
      height: 700px;
    }
    .info{
      order: 1;
      width: 50%;
    }
    .info__services{
      flex-wrap: wrap;
      margin-top: 0;
      flex-direction: row;
      height: 100%;
      width: 100%;
      
    }
    .info__services-item{
      justify-content: flex-start;
      text-align: center;
      height: 270px;
      
      margin-bottom: 10px;

    }
    .info__services-item:nth-child(1){
      flex-basis: 40%;
      order: 1;
      
    }
    .info__services-item:nth-child(2){
      flex-basis: 87%;
      order: 3;
      height: 200px;
    }
    .info__services-item:nth-child(3){
      flex-basis: 40%;
      order: 2;
    }

    .info__services-item h2{
      font-size: 13px;
      height: 20px;
      margin-bottom: 0;
    }
    .info__services-item p{
      font-size: 11px;
      margin-top: 5px;
      line-height: 15px;

    }
    .aktuelles{
      order: 2;
      margin-top: 0;
      border-top-right-radius: 20%;
      width: 100%;
    }
    .aktuelles__title{
      width: 100%;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
    }
    .aktuelles__title h1{
      width: 30%;
    }
    
    .aktuelles__title p{
      width: 60%;
      padding-top: 20px;
      
    }
    .aktuelles__cards{
      justify-content: space-evenly;
      
      gap: 0;
      width: 100%;

    }
    .aktuelles__cards-card-info h2{
      font-size: 12px;
    }

    .mission{
      order: 4;
    }
    footer{
      order: 5;
    }
    
  }
  @media only screen and (min-width: 1201px) {
    nav{
      width: 50%;
    }
    .info{
      width: 50%;
      margin-bottom: 20px;
      height: 380px;
    }
    .info__services-item{
      justify-content: flex-start;
      height: 310px;
      text-align: center;
    }

    .info__services-item h2{
      display: flex;
      justify-content: center;
      align-items: center;
      height: 90px;
      text-align: center;
      margin-bottom: 10px;
    }
    .info__services-item p{
      margin-top: 0;
      text-align: center;
    }
    .aktuelles{
      overflow: hidden;
      height: 200px;
      margin-bottom: 0;
    }
    .aktuelles__title p{
      width: 290px;
    }
    .aktuelles__cards{
     
      display: none;
     
    }
    .mission{
      margin-top: -30px;
    }
   
  }
    
  
